import React from "react";

import Layout from "components/Layout";
import { graphql, Link } from "gatsby";
import styled from "styled-components";

const IndexWrapper = styled.main`
  padding: 0 100px;
`;

const Feed = styled.section`
  margin-bottom: 32px;
`;

const FeedMeta = styled.div``;

const FeedDescription = styled.p`
  font-size: 16px;
  line-height: 26px;
  margin-bottom: 19.5px;
`;

const FeedTitle = styled(Link)`
  font-size: 25px;
  color: black;
  text-decoration: none;
  &:hover,
  &:focus {
    color: gray;
  }
`;

const FeedTime = styled.time`
  font-size: 14px;
  color: #222;
  font-weight: 600;
  text-transform: uppercase;
`;

const FeedCategoryLink = styled(Link)`
  font-size: 14px;
  color: #5d93ff;
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: none;

  &:hover {
    color: gray;
  }
`;

const FeedTags = ({ tags }) => (
  <>
    {tags.map((tag, index) => {
      const comma = index === 0 ? "" : ",";

      return (
        <span key={index}>
          {comma}
          <FeedCategoryLink to={`/tags/${tag}`}> {tag}</FeedCategoryLink>
        </span>
      );
    })}
  </>
);

const Introduction = ({ location, data }) => {
  const path = location && location.pathname && location.pathname.split("/")[1];
  console.log(data);

  return (
    <Layout path={path}>
      <IndexWrapper>
        {data.allMdx.nodes.map(({ id, frontmatter, fields }) => {
          const { title, description, date, tags } = frontmatter;
          return (
            <Feed key={id}>
              <FeedTitle to={`/blog${fields.slug}`}>{title}</FeedTitle>
              <FeedDescription>{description}</FeedDescription>
              <FeedMeta>
                <FeedTime>{date}</FeedTime>
                <FeedTags tags={tags} />
              </FeedMeta>
            </Feed>
          );
        })}
      </IndexWrapper>
    </Layout>
  );
};

export default Introduction;

export const query = graphql`
  query {
    allMdx(
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { published: { eq: true } } }
    ) {
      nodes {
        id
        excerpt(pruneLength: 250)
        frontmatter {
          title
          description
          date(formatString: "MMMM D, YYYY")
          tags
        }
        fields {
          slug
        }
      }
    }
  }
`;
